.ui.search > .results {
  overflow-y: auto;
  max-height: 348px;
}

.startForm {
  position: absolute;
  left: 5rem;
  right: 5rem;
  padding-top: 1rem;
  z-index: 1;
}

.resultsForm {
  height: 95vh;
  overflow-y: scroll;
  max-height: 95vh;
  position: -webkit-sticky;
  position: sticky;
  z-index: 1;
  width: 35rem;
  overflow-x: hidden;
}

.startMap {
  position: absolute;
  height: 95vh;
  width: 100%;
  z-index: 0;
}
.resultsMap {
  position: absolute;
  height: 95vh;
  left: 35rem;
  right: 0px;
  z-index: 0;
}

.expand_route {
  position: absolute;
  left: 93%;
}

.ui.menu {
  z-index: 1;
  margin-bottom: 0;
  min-height: 25px;
  height: 5vh;
  border-radius: 0px;
}

.routePreview {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  margin-left: 5px;
  overflow: hidden;
  position: relative;
  white-space: nowrap;
}

.routePreviewContainer {
  position: relative;
  left: 8%;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 95%;
  text-align: start;
}

.divided {
  max-width: 96%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.divider {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  border-bottom: 5px solid;
  margin: 5px;
}

.previewDeparture {
  position: relative;
  padding-right: 5px;
}

.previewDuration {
  position: relative;
}

.toggleSearchTime {
  position: relative;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-inline-flex;
  display: inline-flex;
}

.timeInput {
  max-width: 150px;
}

.timeline {
  position: relative;
  max-width: 95%;
  list-style: none;
}

.timelineSpacer::before {
  content: "";
  border-left-width: 5px;
  border-left-color: blue;
  border-left-style: solid;
  position: absolute;
  left: 30%;
  height: 200%;
  top: 1rem;
}

.timelineEvent {
  position: relative;
  margin-left: 1rem;
  padding-left: 0;
}

.timelineInfo {
  position: relative;
}

.timelineFrom {
  position: relative;
  margin-left: 2rem;
}

.timelineSpacer {
  background-color: blue;
  display: block;
  border-radius: 50%;
  margin-top: 0.5em;
  position: absolute;
  top: 0;
  left: -1rem;
  width: 1rem;
  height: 1rem;
}
.timelineBody {
  padding: 2em 2em 0 0em;
  position: relative;
  top: -1.875em;
  left: 2em;
  width: 95%;
}
.timelineTime {
  color: white;
  background-color: blue;
  box-shadow: inset 0 0 0 0em #ef795a;
  display: inline-block;
  margin-bottom: 1.2em;
  padding: 0.25em 1em 0.2em 1em;
}

